import { Injectable } from '@angular/core';
import { HTTP } from '@awesome-cordova-plugins/http/ngx';
import { environment } from 'src/environments/environment';
import { TranslateConfigService } from '../translate-config/translate-config.service';
import { TranslateService } from '@ngx-translate/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { from, tap, timeout } from 'rxjs';
import { Capacitor } from '@capacitor/core';

@Injectable({
  providedIn: 'root'
})
export class ClaimApiService {
  readonly tncenUrl: string = environment.baseAEM + environment.tncenUrl;
	readonly tnckmUrl: string = environment.baseAEM + environment.tnckmUrl;

  constructor(
    private http: HTTP,
    private httpClient: HttpClient,
    private translateConfigService: TranslateConfigService,
		public translate: TranslateService,
  ) { }

  /**
  * prestige club tnc
  * @returns 
  */
  async getClaimTNC(path) {
    const url = environment.baseAEM + environment.claimTNCUrl + path;

    const body = {};

    const headers = new HttpHeaders({
      "strict-transport-security": 'max-age=15768000',
      "x-xss-protection": '1; mode=block',
    });

    if (Capacitor.getPlatform() == 'ios' || Capacitor.getPlatform() == 'android') {
      this.http.setDataSerializer("json");
      return from(this.http.get(
        url,
        body,
        {}
      )).pipe(
        tap(),
        timeout(30000)
      );
    }

    return this.httpClient.get(url, {
      headers: headers
    }).pipe(
        tap(),
        timeout(30000)
      );
  }

  /**
   * get term and condition (TNC API)
   * @returns
   */
  async getTNC() {
    const body = {}

    const headers = new HttpHeaders({
      "strict-transport-security": 'max-age=15768000',
      "x-xss-protection": '1; mode=block',
    });

    if (Capacitor.getPlatform() == 'ios' || Capacitor.getPlatform() == 'android') {
      console.log("mobile")
      this.http.setDataSerializer("json");
      return from(this.http.get(
        this.translateConfigService.getCurrentLang() == "km" ? this.tnckmUrl : this.tncenUrl,
        body,
        {}
      )).pipe(
        tap(),
        timeout(30000)
      );;
    } else {
      console.log("web")
      return this.httpClient.get(
        this.translateConfigService.getCurrentLang() == "km" ? this.tnckmUrl : this.tncenUrl, 
        {
          headers: headers
        }
      ).pipe(
        tap(),
        timeout(30000)
      );
    }
  }
}
